import map from 'lodash-es/map.js'
import keys from 'lodash-es/keys.js'
import genID from 'wsemi/src/genID.mjs'
import dtmapping from 'wsemi/src/dtmapping.mjs'
import dtpick from 'wsemi/src/dtpick.mjs'
import nowms2str from 'wsemi/src/nowms2str.mjs'
import now2strp from 'wsemi/src/now2strp.mjs'


let keyTable = 'grups'
let tableNameCht = ''
let tableNameEng = 'Permissions groups'


let settings = {
    id: {
        pk: true,
        name: '',
        type: 'STRING',
    },
    order: {
        name: '',
        type: 'INTEGER',
    },
    name: {
        name: '',
        type: 'STRING',
    },
    description: {
        name: '',
        type: 'TEXT',
    },
    from: {
        name: '', //
        type: 'STRING',
    },
    cpemis: {
        name: '', //json
        type: 'TEXT',
    },
    userId: {
        name: '',
        type: 'STRING',
    },
    timeCreate: {
        name: '',
        type: 'STRING',
    },
    userIdUpdate: {
        name: '',
        type: 'STRING',
    },
    timeUpdate: {
        name: '',
        type: 'STRING',
    },
    // isActive: {
    //     name: '',
    //     type: 'STRING',
    // },
}

let funNew = (ndata = {}) => {
    let o = dtmapping(ndata, keys(settings))
    o.id = `${now2strp()}-${genID()}`
    o.userIdUpdate = o.userId
    o.timeCreate = nowms2str()
    o.userIdUpdate = o.userId
    o.timeUpdate = o.timeCreate
    // o.isActive = 'y'
    return o
}

let funTest = () => {
    let rs = [

        ['M1', `
            {
                'P1': {
                    'mode': 'OR',
                    'isActive': 'y',
                },
                'P2': {
                    'mode': 'OR',
                    'isActive': 'y',
                },
            }
        `],
        ['M2', `
            {
                'P2': {
                    'mode': 'OR',
                    'isActive': 'y',
                },
                'P3': {
                    'mode': 'OR',
                    'isActive': 'y',
                },
            }
        `],
        ['M3', `
            {
                'P2': {
                    'mode': 'OR',
                    'isActive': 'y',
                },
                'P3': {
                    'mode': 'AND',
                    'isActive': 'y',
                },
            }
        `],
        ['M4', `
            {
                'P3': {
                    'mode': 'OR',
                    'isActive': 'y',
                },
                'P4': {
                    'mode': 'OR',
                    'isActive': 'y',
                },
            }
        `],

    ]
    rs = map(rs, ([name, cpemis], k) => {
        let v = funNew({
            userId: 'id-for-admin',
            order: k,
            name,
            description: `${name}`,
            cpemis,
        })
        v.id = `id-for-${name}`
        v = dtpick(v, keys(settings))
        return v
    })
    console.log(`: ${keyTable} `, rs)
    return rs
}

let tab = {
    keyTable,
    tableNameCht,
    tableNameEng,
    settings,
    funNew,
    funTest,
}


export default tab

