import map from 'lodash-es/map.js'
import keys from 'lodash-es/keys.js'
import genID from 'wsemi/src/genID.mjs'
import dtmapping from 'wsemi/src/dtmapping.mjs'
import dtpick from 'wsemi/src/dtpick.mjs'
import nowms2str from 'wsemi/src/nowms2str.mjs'
import now2strp from 'wsemi/src/now2strp.mjs'


let keyTable = 'targets'
let tableNameCht = ''
let tableNameEng = 'Targets'


let settings = {
    id: {
        pk: true,
        name: '', //, ., unicode
        type: 'STRING',
    },
    order: {
        name: '',
        type: 'INTEGER',
    },
    description: {
        name: '',
        type: 'TEXT',
    },
    from: {
        name: '', //
        type: 'STRING',
    },
    userId: {
        name: '',
        type: 'STRING',
    },
    timeCreate: {
        name: '',
        type: 'STRING',
    },
    userIdUpdate: {
        name: '',
        type: 'STRING',
    },
    timeUpdate: {
        name: '',
        type: 'STRING',
    },
    // isActive: {
    //     name: '',
    //     type: 'STRING',
    // },
}

let funNew = (ndata = {}) => {
    let o = dtmapping(ndata, keys(settings))
    o.id = `${now2strp()}-${genID()}`
    o.userIdUpdate = o.userId
    o.timeCreate = nowms2str()
    o.userIdUpdate = o.userId
    o.timeUpdate = o.timeCreate
    // o.isActive = 'y'
    return o
}

let funTest = () => {
    let rs = [

        'A/A/A',
        'A/A/A/',
        'A/A/A/',

        'A/B/A',
        'A/B/A/',
        'A/B/A/',

        'A/B/B/',
        'A/B/B/',

        'A/C',

        'B/A/A',
        'B/A/A/',
        'B/A/A/',
        'B/A/B',
        'B/A/B/',
        'B/A/B/',
        'B/A/C',

        'B/B/A',
        'B/B/A/',
        'B/B/A/',
        'B/B/B',
        'B/B/B/',
        'B/B/B/',

    ]
    rs = map(rs, (key, k) => {
        let v = funNew({
            userId: 'id-for-admin',
            order: k,
            description: `${key}`,
        })
        v.id = key
        v = dtpick(v, keys(settings))
        return v
    })
    console.log(`: ${keyTable} `, rs)
    return rs
}

let tab = {
    keyTable,
    tableNameCht,
    tableNameEng,
    settings,
    funNew,
    funTest,
}


export default tab

