import map from 'lodash-es/map.js'
import keys from 'lodash-es/keys.js'
import genID from 'wsemi/src/genID.mjs'
import dtmapping from 'wsemi/src/dtmapping.mjs'
import dtpick from 'wsemi/src/dtpick.mjs'
import nowms2str from 'wsemi/src/nowms2str.mjs'
import now2strp from 'wsemi/src/now2strp.mjs'


let keyTable = 'pemis'
let tableNameCht = ''
let tableNameEng = 'Permissions'


let settings = {
    id: {
        pk: true,
        name: '',
        type: 'STRING',
    },
    order: {
        name: '',
        type: 'INTEGER',
    },
    name: {
        name: '',
        type: 'STRING',
    },
    description: {
        name: '',
        type: 'TEXT',
    },
    from: {
        name: '', //
        type: 'STRING',
    },
    crules: {
        name: '', //json
        type: 'TEXT',
    },
    userId: {
        name: '',
        type: 'STRING',
    },
    timeCreate: {
        name: '',
        type: 'STRING',
    },
    userIdUpdate: {
        name: '',
        type: 'STRING',
    },
    timeUpdate: {
        name: '',
        type: 'STRING',
    },
    // isActive: {
    //     name: '',
    //     type: 'STRING',
    // },
}

let funNew = (ndata = {}) => {
    let o = dtmapping(ndata, keys(settings))
    o.id = `${now2strp()}-${genID()}`
    o.userIdUpdate = o.userId
    o.timeCreate = nowms2str()
    o.userIdUpdate = o.userId
    o.timeUpdate = o.timeCreate
    // o.isActive = 'y'
    return o
}

let funTest = () => {
    let rs = [

        ['P1', `{  "A/A/A": 'y', "A/B/A": 'n', "A/C": 'n' }`],
        ['P2', `{  "A/C": 'y', "B/A/A": 'y', "B/A/B": 'y' }`],
        ['P3', `{  "A/C": 'y', "B/A/A": 'n', "B/A/B": 'y', "B/A/C": 'y' }`],
        ['P4', `{  "B/B/A/": 'y', "B/B/B/": 'n' }`],

    ]
    rs = map(rs, ([name, crules], k) => {
        let v = funNew({
            userId: 'id-for-admin',
            order: k,
            name,
            description: `${name}`,
            crules,
        })
        v.id = `id-for-${name}`
        v = dtpick(v, keys(settings))
        return v
    })
    console.log(`: ${keyTable} `, rs)
    return rs
}

let tab = {
    keyTable,
    tableNameCht,
    tableNameEng,
    settings,
    funNew,
    funTest,
}


export default tab

